.info {
  position: relative;
  padding: 60px 0 100px;
  overflow: hidden;
  &:after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 0);
    width: 1280px;
    height: 1px;
    background: #DEDEDE;
    pointer-events: none;
  }
}
.cleaning-main3 {
  &:after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 0);
    width: 1280px;
    height: 1px;
    background: #DEDEDE;
    pointer-events: none;
  }
  .cleaning-main__cards {
    border-bottom: 1px solid #C5C5C5;
    padding-bottom: 50px;
  }
  .cleaning-main__card {
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: #F9E508;
      border-radius: 10px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.125s;
    }
    &:hover {
      &:after {
        opacity: 1;
      }
      .cleaning-main__card-desc {
        top: 0;
        bottom: unset;
        color: #000;
      }
      .cleaning-main__card-title {
        color: #000;
        &:hover {
          opacity: 0.7;
        }
      }
      .cleaning-main__card-text {
        display: block;
      }
    }
    &-img {
      &:after {
        background: linear-gradient(180.52deg, rgba(0, 0, 0, 0) 3.671%,rgb(0, 0, 0) 113.689%);
      }
    }
    &-desc {
      height: auto;
      gap: 30px;
      transition: all 0.3s;
    }
    &-title {
      transition: unset;
      &:hover {
        color: #fff;
      }
      wbr {
        display: none;
      }
    }
    &-text {
      display: none;
    }
  }
}
.cleaning-main4 {
  padding: 0 0 100px;
  .cleaning-main__cards {
    border: none;
    padding: 0;
  }
}
.info2 {
  padding: 60px 0 50px;
  &--1 {
    padding: 50px 0 0 0;
    .info2__items {
      margin: 0;
    }
  }
  &--2 {
    padding: 50px 0 110px;
    .info2__items {
      margin: 0;
    }
  }
  &--3 {
    padding: 50px 0;
    .info2__items {
      margin: 0;
    }
    .info2__item {
      &:nth-child(even) {
       .info2__item-desc {
         background: #F9E508;
       }
      }
      &:nth-child(odd) {
        .info2__item-desc {
          background: #fff;
        }
      }
    }
  }
  &__title {
    margin-bottom: 30px;
  }
  &__items {
    margin-top: 100px;
  }
  &__item {
    display: grid;
    grid-template-columns: repeat(2, calc(50%));
    &:nth-child(odd) {
      .info2__item-desc {
        background: #F9E508;
        padding-left: 30px;
        padding-right: 25px;
      }
      .text {
        margin-left: auto;
      }
    }
    &:nth-child(even) {
      .info2__item-desc {
        padding-left: 25px;
        padding-right: 30px;
      }
    }
    &-desc,
    &-desc2 {
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding-top: 30px;
      padding-bottom: 30px;
    }
    &-desc2 {
      padding-left: 25px;
      padding-right: 30px;
      .text {
        margin-left: 0 !important;
      }
    }
    &-img {
      img {
        width: 100%;
        height: 100%;
      }
    }
    .text {
      max-width: 616px;
      p {
        font-size: 18px;
      }
    }
  }
}
.products2 {
  padding: 50px 0;
  overflow: hidden;
  .tabs-head {
    max-width: 1100px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 18px;
    margin: 0 auto;
  }
  .tab {
    height: 54px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    border: 2px solid #DFDFDF;
    border-radius: 20px;
    padding: 5px 20px;
    cursor: pointer;
    transition: all 0.3s;
    &--active {
      border-color: #151515;
      span {
        color: #151515;
      }
    }
    span {
      font-family: 'Montserrat';
      font-size: 18px;
      line-height: 120%;
      font-weight: 500;
      color: #636363;
    }
  }
  &__group {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    margin-top: 50px;
  }
  &__group2 {
    justify-content: flex-end;
    margin: 0;
  }
  .nice-select {
    font-weight: 600;
  }
  &__subgroup {
    display: flex;
    align-items: center;
    gap: 30px;
    .nice-select {
      font-weight: 400;
    }
  }
  .sorting {
    display: flex;
    align-items: center;
    gap: 15px;
    &-btn {
      width: 19px;
      background: transparent;
      padding: 0;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  .tabs-body {
    margin-top: 30px;
  }
  .tab__content {
    display: none;
    &--active {
      display: block;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 72px / 4));
    gap: 24px;
  }
}
.products2--1 {
  padding-top: 0 !important;
  .products2__cards {
    margin-top: 30px;
  }
}
.products2-main {
  position: relative;
  padding-bottom: 100px;
  &:after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 0);
    width: 1280px;
    height: 1px;
