    pointer-events: none;
  }
}
.product2-card {
  display: flex;
  flex-direction: column;
  &__top {
    position: relative;
    background: #F5F5F5;
    border-radius: 10px;
    padding: 10px 10px 125% 10px;
  }
  &__img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 192px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      max-width: calc(100% - 20px);
      max-height: calc(100% - 20px);
    }
  }
  &__desc {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    margin-top: 12px;
  }
  &__title {
    font-family: 'Montserrat';
    font-size: 18px;
    line-height: 120%;
    font-weight: 600;
    &:hover {
      color: #F99D08;
    }
  }
  &__text {
    font-family: 'Montserrat';
    font-size: 14px;
    line-height: 120%;
    color: #828282;
    margin-top: 15px;
    margin-bottom: auto;
  }
  &__btn {
    width: 100%;
    height: 58px;
    gap: 10px;
    background: transparent;
    border: 1px solid #FFCC00;
    border-radius: 20px;
    padding: 5px 10px;
    margin-top: 15px;
    i {
      width: 21px;
      height: 21px;
      flex-shrink: 0;
      border: 1px solid #242222;
      border-radius: 4px;
    }
    span {
      font-size: 18px;
      line-height: 120%;
      font-weight: 600;
    }
  }
}
.advantages {
