  overflow: hidden;
  &__slider {
    position: relative;
    padding: 0 60px;
    margin-top: 60px;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-button-prev,
  .swiper-button-next {
    width: 38px;
    height: 38px;
  }
  .swiper-button-prev {
    left: 0;
  }
  .swiper-button-next {
    right: 0;
  }
  .swiper-pagination {
    display: none;
  }
}
.advantage-card {
