        bottom: -0.938em;
        transform: rotate(58deg);
        width: 2.25em;
        height: 0.125em;
        background: #000;
        &:after {
          left: 1.25em !important;
          bottom: -0.75em !important;
        }
        &:before {
          left: 1.563em !important;
          bottom: -0.438em !important;
        }
      }
    }
    &--4 {
      right: 8.125em;
      bottom: 11.875em;
      .line2 {
