        height: 0.125em;
        background: #000;
      }
    }
    &--5 {
      left: 3.438em;
      bottom: 8.75em;
      .line2 {
        right: -1.75em;
        bottom: -0.938em;
        transform: rotate(58deg);
        width: 2.25em;
        height: 0.125em;
        background: #000;
        &:after {
          left: 1.25em !important;
          bottom: -0.75em !important;
        }
        &:before {
          left: 1.563em !important;
          bottom: -0.438em !important;
        }
      }
    }
    &--6 {
      right: 5em;
      bottom: 6.875em;
      .line2 {
        left: -1.75em;
        bottom: -0.938em;
        transform: rotate(-58deg);
        width: 2.25em;
        height: 0.125em;
        background: #000;
      }
    }
    &-text {
      font-style: italic;
