      font-size: 1.125em;
      line-height: 120%;
    }
    .line {
      width: 100%;
      height: 0.125em;
      background: #000;
    }
    .line2 {
      position: absolute;
      &:after {
        content: '';
        position: absolute;
        left: -0.375em;
        bottom: -0.75em;
        width: 1.438em;
        height: 1.438em;
        background: #fff;
        border: 0.125em solid #000;
        border-radius: 50%;
      }
      &:before {
        content: '';
        position: absolute;
        z-index: 1;
        left: -0.063em;
        bottom: -0.438em;
        width: 0.813em;
        height: 0.813em;
        background: #000;
        border-radius: 50%;
      }
    }
  }
