      grid-template-columns: 100%;
      gap: 30px;
      padding: 0 15px;
    }
    &__item {
      grid-template-columns: 100%;
      gap: 30px;
      &-desc {
        order: -1;
        background: #fff !important;
        padding: 0 !important;
        margin: 0 !important;
      }
      &-desc2 {
        padding: 0 !important;
        margin: 0 !important;
      }
      .text {
        max-width: 100%;
      }
    }
  }
  .products2 {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
    }
  }
  .advantages {
    &__slider {
      padding: 0 0 30px 0;
    }
    .swiper {
      overflow: unset;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
    .swiper-pagination {
      bottom: 0;
      display: block;
    }
  }
  .types {
    .knowledge__slider-nav {
      display: none;
    }
    .swiper-pagination {
      bottom: 0;
      display: block;
    }
  }
  .classes {
    &__images {
      grid-template-columns: 100%;
    }
  }
  .info3 {
    &__item {
      flex-direction: column;
      gap: 0;
      &-img {
        width: 100%;
        margin-bottom: 15px;
      }
      &-desc {
        width: 100%;
      }
      .no-picture {
        margin: 0;
      }
    }
  }
  .video {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
      gap: 15px;
    }
  }
}

@media screen and (max-width: 576px) {
  .cleaning-main3 {
    .cleaning-main__card {
      &-title {
        font-size: 14px;
      }
      &-text {
        font-size: 12px;
      }
    }
  }
  .cleaning-main4 {
    padding: 0 0 60px;
  }
  .products2 {
    &__group {
      flex-direction: column;
      align-items: start;
    }
    &__subgroup {
      width: 100%;
      justify-content: space-between;
      gap: 15px;
    }
  }
  .video-card {
    &__title {
      font-size: 16px;
    }
  }
  .video2 {
    .text {
      h3 {
        font-size: 18px;
      }
    }
  }
  .product {
    &__wrap {
      max-width: 16em;
    }
    &__item {
      max-width: 8em;
      &--1 {
        top: -3em;
        right: -3.5em;
      }
      &--2 {
        top: 0.875em;
        left: 0.5em
      }
      &--3 {
        left: -1em;
        bottom: 7.75em;
      }
      &--4 {
        right: -0.5em;
        bottom: 5em;
      }
      &--5 {
        left: -2.562em;
        bottom: 3.75em;
      }
      &--6 {
        right: -1.8em;
        bottom: 0;
        .line2 {
          top: 0.7em;
          left: -1.6em;
          bottom: unset;
          transform: rotate(64deg);
        }
      }
      &-text {
        padding-bottom: 0.313em;
        font-size: 0.625em;
      }
      .line2 {
        &:after {
          width: 1em;
          height: 1em;
        }
        &:before {
          width: 0.375em;
          height: 0.375em;
        }
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .cleaning-main3 {
    .cleaning-main__card {
      &-title {
        font-size: 12px;
      }
      &-text {
        font-size: 10px;
      }
    }
  }
}

@media screen and (max-width: 399px) {
  .products2 {
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .video {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}
