.main {
  background: #FFFCDA;
  padding: 30px 0;
  overflow: hidden;
  &__inner {
    display: flex;
    align-items: center;
    gap: 35px;
  }
  &__desc {
    position: relative;
    z-index: 2;
    max-width: 760px;
    width: calc(100% - 385px);
  }
  &__img {
    position: relative;
    width: 350px;
    flex-shrink: 0;
    &:before {
      content: '';
      position: absolute;
      top: 55%;
      left: 45%;
      transform: translate(-50%, -50%) rotate(-42deg);
      width: 585px;
      height: 270px;
      background: transparent;
      border: 2px solid #F9E508;
      border-radius: 50%;
      pointer-events: none;
    }
    img {
      position: relative;
      z-index: 2;
      width: 100%;
    }
  }
  &__title {
    font-size: 40px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__text {
    margin-top: 20px;
  }
  &__btn {
    height: 70px;
    border-radius: 30px;
    padding: 5px 50px;
    margin-top: 40px;
    span {
      font-size: 24px;
      font-weight: 600;
    }
  }
}
.products {
  padding: 100px 0 50px;
  overflow: hidden;
  &__text {
    margin-top: 20px;
  }
  .tabs {
    margin-top: 30px;
  }
  .tabs-head {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
  }
  .tabs-btns {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px;
  }
  .tab {
    height: 54px;
    border: 2px solid #DFDFDF;
    border-radius: 20px;
    padding: 5px 20px;
    font-family: 'Montserrat';
    font-size: 18px;
    line-height: 120%;
    font-weight: 500;
    color: #636363;
    cursor: pointer;
    transition: all 0.3s;
    &--active {
      background: #1B1C1D;
      border-color: #1B1C1D;
      color: #fff;
      &:hover {
        color: #fff !important;
      }
    }
    &:hover {
      border-color: #1B1C1D;
      color: #1B1C1D;
    }
    &:active {
      background: #1B1C1D;
      color: #fff;
    }
  }
  &__link {
    span {
      text-decoration: unset;
    }
  }
  .tabs-body {
    margin-top: 20px;
  }
  .tab__content {
    display: none;
    &--active {
      display: block;
    }
  }
  &__slider {
    position: relative;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-button-prev {
    left: -85px;
  }
  .swiper-button-next {
    right: -85px;
  }
  .swiper-pagination {
    display: none;
  }
}
.product-card {
  height: auto;
  display: flex;
  flex-direction: column;
  border: 1px solid #C9C9C9;
  border-radius: 20px;
  padding: 25px;
  &__top {
    position: relative;
  }
  .status {
    position: absolute;
    top: 0;
    right: 0;
    min-width: 56px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    clip-path: polygon( 100% 100%,18.106% 100%,0% 50%,18.106% 0%,100% 0%,100% 100% );
    padding: 5px 10px;
    font-family: 'Montserrat';
    font-size: 14px;
    line-height: 100%;
    font-weight: 700;
    text-transform: uppercase;
    color: #fff;
  }
  .hit {
    background: #A6DB85;
  }
  .new {
    background: #85B2DA;
    padding-right: 5px;
  }
  &__img {
    height: 230px;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      max-width: 90%;
      max-height: 100%;
    }
  }
  &__desc {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: start;
    margin-top: 20px;
  }
  &__title {
    font-family: 'Montserrat';
    font-size: 20px;
    line-height: 120%;
    font-weight: 600;
    &:hover {
      color: #F99D08;
    }
  }
  &__text {
    font-size: 16px;
    line-height: 120%;
    color: #747474;
    margin-top: 15px;
    margin-bottom: auto;
  }
  &__btn {
    height: 50px;
    border-radius: 20px;
    padding: 5px 30px;
    margin-top: 25px;
    span {
      font-size: 18px;
      font-weight: 600;
    }
  }
}
.categories {
  padding: 50px 0 100px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 30px;
  }
  &__group {
    width: 295px;
  }
  &__title {
    position: relative;
    padding-bottom: 14px;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 80px;
      height: 4px;
      background: #F9E508;
      border-radius: 5px;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 15px;
    margin-top: 40px;
    &-icon {
      width: 68px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      img {
        width: 100%;
      }
    }
    &-number {
      font-size: 26px;
      line-height: 120%;
      font-weight: 600;
      color: #535353;
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      font-weight: 600;
      color: #535353;
      margin-top: 5px;
    }
  }
  &__link {
    margin-top: 40px;
  }
  &__cards {
    width: calc(100% - 325px);
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    gap: 24px;
  }
}
.category-card {
  background: #F5F5F5;
  border-radius: 10px;
  padding: 20px;
  &__img {
    height: 180px;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title {
    display: inline-block;
    font-family: 'Montserrat';
    font-size: 18px;
    line-height: 120%;
    font-weight: 600;
    margin-top: 15px;
    &:hover {
      color: #F99D08;
    }
  }
}
.knowledge {
  background: #F5F5F5;
  padding: 40px 0;
  overflow: hidden;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
  }
  &__flex {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 25px;
    margin-top: 30px;
  }
  &__slider {
    position: relative;
    width: calc(100% - 305px);
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  &__slider-nav {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  .swiper-button-prev,
  .swiper-button-next {
    position: static;
    margin: 0;
  }
  .swiper-pagination {
    display: none;
  }
}
.knowledge-card {
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: start;
  background: #fff;
  padding: 20px;
  &2 {
    width: 280px;
    justify-content: flex-end;
    background-image: url("../img/knowledge-img.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    color: #fff;
    .knowledge-card__desc {
      margin-top: 0;
    }
    .knowledge-card__title {
      &:hover {
        color: #fff;
      }
    }
  }
  &__img {
    position: relative;
    width: 100%;
    padding-bottom: 76%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__desc {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: start;
    margin-top: 10px;
  }
  &__title {
    font-family: 'Montserrat';
    font-size: 18px;
    line-height: 120%;
    font-weight: 600;
    &:hover {
      color: #F99D08;
    }
  }
  &__text {
    font-size: 16px;
    line-height: 140%;
    margin-top: 12px;
    margin-bottom: auto;
  }
  &__btn {
    height: 50px;
    border-radius: 20px;
    padding: 5px 30px;
    margin-top: 20px;
    span {
      font-size: 18px;
      font-weight: 600;
    }
  }
}
.questions {
  overflow: hidden;
  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 30px;
    padding: 100px 0;
    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 1280px;
      height: 1px;
      background: #DEDEDE;
      pointer-events: none;
    }
  }
  &__group {
    width: 284px;
  }
  &__title {
    max-width: 225px;
  }
  &__link {
    margin-top: 30px;
  }
  &__items {
    width: calc(100% - 314px);
  }
  &__item {
    border-bottom: 1px solid #B2B2B2;
    padding: 20px 0;
    &:first-child {
      border-top: 1px solid #B2B2B2;
    }
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 15px;
      cursor: pointer;
    }
    .item-show--active {
      .questions__item-btn {
        transform: rotate(-45deg);
        svg path {
          fill: #000;
        }
      }
    }
    &-title {
      max-width: 600px;
      font-size: 24px;
      line-height: 120%;
      font-weight: 600;
    }
    &-btn {
      width: 14px;
      flex-shrink: 0;
      transition: all 0.125s;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.3s;
      }
    }
    &-hidden {
      display: none;
      margin-top: 15px;
    }
    &-text {
      max-width: 580px;
      font-size: 18px;
      line-height: 120%;
      color: #818181;
    }
  }
}

@media screen and (max-width: 1500px) {
  .products {
    .swiper-button-prev {
      left: -27px;
    }
    .swiper-button-next {
      right: -28px;
    }
  }
}

@media screen and (max-width: 1300px) {
  .main {
    &__img {
      &:before {
        left: 40%;
        width: 550px;
        height: 250px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .categories {
    &__group {
      width: 260px;
    }
    &__cards {
      width: calc(100% - 290px);
    }
  }
  .knowledge-card {
    @media screen and (min-width: 993px) {
      &__title {
        font-size: 16px;
      }
      &__text {
        font-size: 14px;
      }
      &__btn {
        span {
          font-size: 16px;
        }
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    &__inner {
      gap: 30px;
    }
    &__desc {
      width: calc(100% - 330px);
    }
    &__img {
      width: 300px;
      &:before {
        width: 480px;
        height: 215px;
      }
    }
    &__title {
      font-size: 32px;
    }
    &__text {
      margin-top: 15px;
    }
    &__btn {
      height: 54px;
      border-radius: 20px;
      margin-top: 30px;
      span {
        font-size: 18px;
      }
    }
  }
  .products {
    padding: 60px 0 30px;
    &__text {
      margin-top: 15px;
    }
    &__link {
      span {
        font-size: 16px;
      }
    }
    &__slider {
      padding-bottom: 30px;
    }
    .swiper {
      overflow: unset;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
    .swiper-pagination {
      bottom: 0;
      display: block;
    }
  }
  .product-card {
    padding: 20px 15px;
    &__title {
      font-size: 18px;
    }
    &__text {
      font-size: 14px;
    }
  }
  .categories {
    padding: 30px 0 60px;
    &__inner {
      flex-direction: column;
    }
    &__group {
      width: 100%;
    }
    &__item {
      margin-top: 20px;
      &-icon {
        width: 50px;
      }
      &-number {
        font-size: 20px;
      }
      &-text {
        font-size: 14px;
      }
    }
    &__link {
      margin-top: 20px;
    }
    &__cards {
      width: 100%;
      grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
      gap: 20px;
    }
  }
  .category-card {
    padding: 20px 15px;
  }
  .knowledge {
    &__flex {
      gap: 5px;
    }
    &__slider {
      width: calc(100% - 265px);
    }
  }
  .knowledge-card {
    padding: 20px 15px;
    &2 {
      width: 260px;
    }
  }
  .questions {
    &__inner {
      flex-direction: column;
      padding: 60px 0;
    }
    &__group {
      width: 100%;
    }
    &__link {
      margin-top: 20px;
    }
    &__items {
      width: 100%;
    }
    &__item {
      &-title {
        font-size: 20px;
      }
      &-text {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .main {
    &__inner {
      flex-direction: column;
    }
    &__desc {
      width: 100%;
    }
    &__img {
      width: 100%;
      max-width: 300px;
      margin: 0 auto;
      &:before {
        left: 50%;
        width: 430px;
        height: 180px;
      }
    }
  }
  .product-card {
    &__img {
      height: 200px;
    }
  }
  .categories {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 30px / 3));
      gap: 15px;
    }
  }
  .category-card {
    &__img {
      height: 160px;
    }
  }
  .knowledge {
    &__flex {
      flex-direction: column;
    }
    &__slider {
      width: 100%;
      padding-bottom: 30px;
    }
    &__slider-nav {
      display: none;
    }
    .swiper-pagination {
      bottom: 0;
      display: block;
    }
  }
  .knowledge-card {
    &2 {
      width: 100%;
      min-height: 500px;
    }
  }
}

@media screen and (max-width: 576px) {
  .product-card {
    &__btn {
      width: 100%;
    }
  }
  .categories {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
    }
  }
}

@media screen and (max-width: 399px) {
  .categories {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}