.ecologic-info {
  padding: 60px 0 50px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
  }
  &__img {
    width: 410px;
    img {
      width: 100%;
    }
  }
  &__desc {
    width: calc(100% - 450px);
  }
  &__text {
    margin-top: 20px;
  }
}
.ecologic-advantages {
  padding: 50px 0 100px;
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 36px / 4));
    gap: 20px 12px;
  }
  &__title {
    position: relative;
    padding-bottom: 14px;
    font-family: 'Montserrat';
    font-size: 28px;
    line-height: 120%;
    font-weight: 600;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 80px;
      height: 4px;
      background: #F9E508;
      border-radius: 5px;
    }
  }
  &__link {
    margin-top: 30px;
  }
  &__item {
    background: #fff;
    box-shadow: 0px 1px 3px 0px rgba(102, 102, 102, 0.1),0px 5px 5px 0px rgba(102, 102, 102, 0.09),0px 11px 6px 0px rgba(102, 102, 102, 0.05),0px 19px 8px 0px rgba(102, 102, 102, 0.01),0px 30px 8px 0px rgba(102, 102, 102, 0);
    border-radius: 10px;
    padding: 20px;
    &2 {
      background: transparent;
      box-shadow: unset;
      border-radius: 0;
      padding: 0;
    }
    &-icon {
      width: 52px;
      height: 52px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #FFF371;
      border-radius: 10px;
      padding: 5px;
      img {
        max-width: 100%;
      }
    }
    &-desc {
      margin-top: 20px;
    }
    &-title {
      font-family: 'Montserrat';
      font-size: 18px;
      line-height: 120%;
      font-weight: 600;
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      margin-top: 12px;
    }
  }
}
.ecologic-info2 {
  background: #1F1D1D;
  color: #fff;
  padding: 60px 0;
  &__text {
    margin-top: 20px;
  }
}
.ecologic-advantages2 {
  padding: 100px 0;
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 60px;
  }
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 60px;
    &:nth-child(even) {
      .ecologic-advantages2__item-desc {
        order: -1;
      }
    }
    &-img {
      position: relative;
      width: 400px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 400px;
        height: 400px;
        border: 1px solid #FFEF3B;
        border-radius: 50%;
        pointer-events: none;
      }
      &:after {
        content: '';
        position: absolute;
        top: 40px;
        left: 50%;
        transform: translate(-50%, 0);
        width: 320px;
        height: 320px;
        background: #FFFCDA;
        border-radius: 50%;
        pointer-events: none;
      }
      img {
        position: relative;
        z-index: 2;
        max-width: 80%;
        max-height: 100%;
      }
    }
    &-desc {
      width: calc(100% - 460px);
    }
    &-text {
      margin-top: 20px;
    }
  }
}
.ecologic-advantages3 {
  background: #F5F5F5;
  padding: 50px 0;
  &__items {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 30px;
  }
  &__item {
    max-width: 400px;
    &-icon {
      width: 86px;
      height: 86px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #F9E508;
      border-radius: 50%;
      padding: 5px;
      img {
        max-width: 100% ;
      }
    }
    &-desc {
      margin-top: 20px;
    }
    &-title {
      font-family: 'Montserrat';
      font-size: 22px;
      line-height: 120%;
      font-weight: 600;
    }
    &-text {
      display: grid;
      grid-template-columns: 100%;
      gap: 15px;
      margin-top: 15px;
      p {
        font-size: 18px;
        line-height: 120%;
      }
    }
  }
}
.cleaning-main {
  position: relative;
  padding-bottom: 50px;
  overflow: hidden;
  &2 {
    padding: 50px 0 100px;
    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 1280px;
      height: 1px;
      background: #DEDEDE;
      pointer-events: none;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    gap: 24px;
    margin-top: 40px;
  }
  &__img {
    margin-top: 100px;
    img {
      width: 100%;
      min-height: 200px;
    }
  }
}
.cleaning-main__card {
  position: relative;
  overflow: hidden;
  &:hover {
    .cleaning-main__card-img {
      img {
        transform: scale(1.125);
      }
    }
  }
  &-img {
    position: relative;
    width: 100%;
    display: block;
    padding-bottom: 100%;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.62);
      border-radius: 10px;
      pointer-events: none;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 10px;
      transition: all 0.3s;
    }
  }
  &-desc {
    position: absolute;
    z-index: 2;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-content: start;
    gap: 10px;
    padding: 24px;
    color: #fff;
  }
  &-title {
    font-size: 22px;
    line-height: 120%;
    font-weight: 600;
    text-transform: uppercase;
  }
  &-link {
    font-size: 18px;
    line-height: 120%;
    text-decoration: underline;
    &:hover {
      text-decoration: unset;
    }
  }
  &-text {
    font-size: 18px;
    line-height: 120%;
  }
}
.cleaning-info {
