      margin-top: 0;
    }
  }
  &__5 {
    padding: 50px 0 0 0;
  }
  &__6 {
    padding-bottom: 50px;
  }
  &__7 {
    padding: 0;
  }
  &__8 {
    padding: 50px 0;
  }
  &__9 {
    padding: 50px 0 25px;
  }
  &__10 {
    padding: 25px 0 50px;
  }
  &__11 {
    padding: 50px 0 100px;
    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
