  }
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 50px;
  }
  &__item2 {
    @media screen and (min-width: 993px) {
      .cleaning-info__img {
        width: 43%;
      }
      .cleaning-info__desc {
        width: 57%;
      }
    }
  }
  &__item3 {
    @media screen and (min-width: 993px) {
      .cleaning-info__img {
        width: 33%;
      }
      .cleaning-info__desc {
        width: 67%;
      }
    }
  }
  &__desc {
    width: 49%;
  }
  &__img,
  &__video {
    width: 51%;
    img {
      width: 100%;
      border-radius: 10px;
    }
  }
  &__video {
    position: relative;
    &-img {
      position: relative;
      display: block;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.42);
        border-radius: 10px;
        pointer-events: none;
      }
      img {
        width: 100%;
        border-radius: 10px;
      }
    }
  }
  &__subtitle {
    margin-bottom: 20px;
  }
  &__img2 {
    margin: 50px 0;
    img {
      width: 100%;
      min-height: 200px;
    }
  }
  &__img3 {
    margin: 50px 0 0 0;
    img {
      width: 100%;
      min-height: 200px;
    }
  }
  &__wrap {
    margin-top: 100px;
  }
  &__grid {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 60px / 2));
    align-items: start;
    gap: 60px;
    margin-top: 30px;
    .text {
      display: grid;
      grid-template-columns: 100%;
      gap: 50px;
    }
  }
  &__btn {
    height: 58px;
    gap: 10px;
    border-radius: 15px;
    padding: 5px 30px;
    margin-top: 35px;
    &:hover {
      i svg path {
        fill: #fff;
      }
    }
    span {
      font-size: 18px;
      font-weight: 600;
    }
    i {
      width: 12px;
      svg {
        width: 100%;
        height: auto;
      }
