  }
  &__img3 {
    max-width: 1536px;
    margin: 50px auto 0 auto;
  }
  .swiper-button-prev,
  .swiper-button-next {
    top: 30%;
    width: 22px;
    height: auto;
    background: transparent;
    border: none;
    svg {
      max-width: 100%;
      width: 100%;
    }
  }
  .swiper-button-prev {
    left: -52px;
  }
  .swiper-button-next {
    right: -52px;
  }
  &--text {
    hr {
      height: 20px;
    }
    ul {
      li {
        display: flex;
        align-items: center;
        gap: 35px;
        padding: 0;
        &:before {
          position: unset;
          width: 72px;
          height: 72px;
          flex-shrink: 0;
