    }
    &__grid {
      grid-template-columns: 100%;
      gap: 30px;
      .text {
        gap: 30px;
      }
    }
    &__btn {
      margin-top: 20px;
      span {
        font-size: 16px;
      }
    }
    &__items3 {
      grid-template-columns: repeat(3, calc(100% / 3 - 30px / 3));
      gap: 15px;
    }
    &__item3 {
