        font-size: 16px;
      }
    }
  }
  .cleaning-info2 {
    &__3 {
      margin-top: 60px;
    }
    &__6 {
      &:before {
        height: calc(100% - 88px) !important;
      }
    }
    &__8 {
      padding: 40px 0;
    }
    &__top {
      margin-bottom: 30px;
    }
    &__slider {
      margin-top: 30px;
    }
    &__img,
    &__img2 {
      margin-top: 30px;
    }
    &__img2 {
      margin-bottom: 30px;
    }
    &__img3 {
      margin: 30px 0 0 0;
    }
    &--text {
      ul {
        li {
          gap: 15px;
          &:before {
            width: 50px;
            height: 50px;
          }
        }
      }
    }
  }
  .cleaning-info2__card {
    &-title {
