    }
  }
  .cleaning-products {
    padding: 60px 0;
  }
  .cleaning-services {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 30px / 3));
      gap: 15px;
    }
    &__btn {
      margin-top: 30px;
    }
  }
  .cleaning-services__card {
    &-link {
      font-size: 16px;
    }
  }
}

@media screen and (max-width: 768px) {
  .ecologic-info {
    &__inner {
      flex-direction: column;
    }
    &__img {
      max-width: 300px;
      width: 100%;
      margin: 0 auto;
    }
    &__desc {
      width: 100%;
      order: -1;
    }
  }
  .ecologic-advantages {
    &__items {
      grid-template-columns: repeat(2, calc(50% - 12px / 2));
    }
