        }
      }
      &-desc {
        width: 100%;
        order: -1 !important;
      }
    }
  }
  .cleaning-main {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
    }
  }
  .cleaning-info {
    &__item {
      flex-direction: column;
    }
    &__desc {
      width: 100%;
      order: -1;
    }
    &__img,
    &__video {
      width: 100%;
    }
  }
  .cleaning-info2 {
