.btn-black {
  background: #1C1C1B;
  color: #fff;
  &:hover {
    background: #F9E508;
    color: #1B1C1D;
  }
  span {
    line-height: 120%;
  }
}
.btn-yellow {
  background: #F9E508;
  color: #1B1C1D;
  &:hover {
    background: #1C1C1B;
    color: #fff;
  }
  span {
    line-height: 120%;
  }
}
.h2 {
  font-family: 'Montserrat';
  font-size: 38px;
  line-height: 120%;
  font-weight: 600;
  text-transform: uppercase;
}
.h3 {
  font-family: 'Montserrat';
  font-size: 32px;
  line-height: 120%;
  font-weight: 600;
  text-transform: uppercase;
}
.text22 {
  display: grid;
  grid-template-columns: 100%;
  gap: 12px;
  p {
    font-size: 22px;
    line-height: 120%;
  }
}
.text28 {
  font-family: 'Montserrat';
  font-size: 28px;
  line-height: 120%;
  font-weight: 600;
}
.swiper-button-prev,
.swiper-button-next {
  width: 55px;
  height: 55px;
  background: transparent;
  border: 1px solid #C9C9C9;
  border-radius: 50%;
  transition: all 0.3s;
  &:after {
    display: none;
  }
  &:hover {
    background: #F9E508;
    border-color: #F9E508;
  }
  svg {
    max-width: 60%;
    height: auto;
  }
}
.swiper-pagination {
  &-bullet-active {
    background: #1F1D1D;
  }
}
.page-top {
  &__wrap {
    background: #F9E508;
    padding: 40px 0;
  }
  &__title {
    max-width: 1005px;
    font-weight: 700;
  }
  &__title--1 {
    max-width: 810px;
  }
  &__text {
    margin-top: 20px;
  }
  &__img {
    img {
      width: 100%;
      min-height: 200px;
    }
  }
}
.breadcrumbs {
  margin-bottom: 30px;
  &__items {
    display: flex;
    flex-wrap: wrap;
    gap: 10px 5px;
  }
  &__item {
    font-size: 18px;
    line-height: 120%;
    &:hover {
      opacity: 0.7;
    }
  }
  &__item--active {
    &:hover {
      opacity: 1;
    }
  }
}
.all__link {
  display: flex;
  align-items: center;
  gap: 10px;
  &:hover {
    color: #F99D08;
    span {
      text-decoration: unset;
    }
    i svg path {
      fill: #F99D08;
    }
  }
  span {
    font-family: 'Montserrat';
    font-size: 18px;
    line-height: 120%;
    font-weight: 600;
    text-transform: uppercase;
    text-decoration: underline;
    transition: all 0.3s;
  }
  i {
    width: 14px;
    svg {
      width: 100%;
      height: auto;
    }
    svg path {
      transition: all 0.3s;
    }
  }
}
.menu-blocks {
  position: relative;
  margin-bottom: 60px;
  @media screen and (min-width: 993px) {
    &2 {
      .swiper-wrapper {
        justify-content: center;
      }
    }
  }
  .swiper-slide {
    position: relative;
    width: auto !important;
    padding-top: 20px;
    font-family: 'Montserrat';
    font-size: 20px;
    line-height: 120%;
    font-weight: 600;
    text-transform: uppercase;
    color: #848484;
    &-active {
      color: #1B1C1D;
      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 3px;
        background: #000;
        border-radius: 5px;
      }
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    top: calc(50% + 20px);
    width: 16px;
    height: auto;
    background: transparent;
    border: none;
    border-radius: 0;
    svg {
      width: 100%;
    }
  }
  .swiper-button-prev {
    left: -46px;
  }
  .swiper-button-next {
    right: -46px;
  }
  .swiper-button-disabled {
    display: none;
  }
}
.text {
  &--border {
    border-top: 1px solid #000;
    border-bottom: 1px solid #000;
  }
  &-grid--33 {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
    gap: 30px;
  }
  &-grid--50 {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 24px;
    nav {
      display: flex;
      flex-direction: column;
      gap: 20px;
      h4,
      h5,
      ol,
      p {
        margin: 0;
      }
      span {
        position: relative;
        padding-bottom: 63%;
        margin: auto 0 0 0;
        img {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          min-height: 200px;
          margin: 0;
        }
      }
    }
  }
  hr {
    height: 30px;
    margin: 0;
    opacity: 0;
    pointer-events: none;
  }
  h2 {
    font-size: 38px;
    line-height: 120%;
    font-weight: 600;
    text-transform: uppercase;
    margin-bottom: 20px;
  }
  h3 {
    font-size: 32px;
    line-height: 120%;
    font-weight: 600;
    text-transform: uppercase;
    margin-bottom: 20px;
  }
  h4 {
    font-size: 28px;
    line-height: 120%;
    font-weight: 600;
    margin-bottom: 20px;
  }
  h5 {
    font-size: 24px;
    line-height: 120%;
    font-weight: 600;
    margin-bottom: 20px;
  }
  p {
    font-size: 22px;
    line-height: 120%;
    margin-top: 12px;
  }
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
    margin-top: 20px;
    &:first-child {
      margin-top: 0;
    }
    li {
      position: relative;
      padding-left: 30px;
      font-size: 22px;
      line-height: 120%;
      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 22px;
        height: 22px;
        background: linear-gradient(120deg, rgb(249, 173, 8) -1.857%,rgba(249, 173, 8, 0.01) 50.251%,rgba(249, 173, 8, 0) 50.256%);
        border-radius: 50%;
      }
      &:after {
        content: '';
        position: absolute;
        z-index: 1;
        top: 4px;
        left: 4px;
        width: 14px;
        height: 14px;
        background: #F9AD08;
        border-radius: 50%;
      }
    }
  }
  ol {
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
    margin-top: 20px;
    li {
      list-style: auto;
      list-style-position: inside;
      font-size: 22px;
      line-height: 120%;
    }
  }
  strong {
    font-weight: 600;
  }
  img {
    width: 100%;
    margin: 20px 0;
  }
}
.text2 {
  display: grid;
  grid-template-columns: 100%;
  gap: 20px;
  h6 {
    font-size: 22px;
    line-height: 120%;
    font-weight: 600;
    margin: 0;
  }
  p {
    font-size: 18px;
    margin: 0;
  }
  ul {
    margin: 0;
    li {
      font-size: 18px;
      &:before {
        width: 18px;
        height: 18px;
      }
      &:after {
        top: 4px;
        width: 10px;
        height: 10px;
      }
    }
  }
}
.list--black {
  ul li {
    &:before {
      background: linear-gradient(120deg, #32312e -1.857%, rgba(50, 49, 46, .01) 50.251%, rgba(50, 49, 46, 0) 50.256%);
    }
    &:after {
      background: #32312e;
    }
  }
}
.nice-select {
  width: auto;
  height: auto;
  background: transparent;
  border: none;
  border-radius: unset;
  padding: 0 24px 0 0;
  font-family: 'Montserrat';
  font-size: 18px;
  line-height: 120%;
  &:after {
    top: 50%;
    right: 0;
    transform: translate(0, -50%);
    width: 12px;
    height: 7px;
    background-image: url("../img/arrow-bottom2.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
  .list {
    background: #fff;
    margin-top: 10px;
    li {
      padding: 10px;
      border: none;
      font-size: 16px;
    }
  }
}
.open {
  &:after {
    transform: translate(0, -50%) rotate(-180deg) !important;
  }
}
.play {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 81px;
  display: block;
  img {
    width: 100%;
  }
}

@media screen and (max-width: 992px) {
  .h2 {
    font-size: 28px;
  }
  .h3 {
    font-size: 26px;
  }
  .text22 {
    gap: 10px;
    p {
      font-size: 18px;
    }
  }
  .text28 {
    font-size: 22px;
  }
  .all__link {
    span {
      font-size: 16px;
    }
  }
  .menu-blocks {
    margin-bottom: 30px;
    .swiper-slide {
      font-size: 18px;
    }
  }
  .text {
    &-grid--33 {
      grid-template-columns: 100%;
    }
    &-grid--50 {
      grid-template-columns: 100%;
    }
    hr {
      height: 20px;
    }
    h2 {
      font-size: 28px;
    }
    h3 {
      font-size: 26px;
    }
    h4 {
      font-size: 24px;
    }
    h5 {
      font-size: 22px;
    }
    h6 {
      font-size: 20px;
    }
    p {
      font-size: 18px;
    }
    ul {
      li {
        font-size: 18px;
        &:before {
          width: 18px;
          height: 18px;
        }
        &:after {
          top: 4px;
          width: 10px;
          height: 10px;
        }
      }
    }
    ol {
      li {
        font-size: 18px;
      }
    }
  }
  .nice-select {
    font-size: 16px;
    .list {
      li {
        font-size: 14px;
      }
    }
  }
  .play {
    width: 60px;
  }
}

@media screen and (max-width: 576px) {
  .text22 {
    p {
      font-size: 16px;
    }
  }
  .text28 {
    font-size: 18px;
  }
  .menu-blocks {
    .swiper-slide {
      font-size: 16px;
    }
  }
  .text {
    h4 {
      font-size: 22px;
    }
    h5 {
      font-size: 20px;
    }
    h6 {
      font-size: 18px;
    }
    p {
      font-size: 16px;
    }
    ul,
    ol {
      li {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 359px) {
  .page-top {
    &__title2 {
      font-size: 26px;
    }
  }
}