.footer {
  padding: 50px 0 80px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 50px;
  }
  &__left {
    display: flex;
    align-items: start;
    gap: 20px;
  }
  &__right {
    max-width: 666px;
    flex-shrink: 0;
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 50px;
  }
  &__logo {
    width: 194px;
    flex-shrink: 0;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    max-width: 315px;
    font-size: 16px;
    line-height: 120%;
    color: #828282;
  }
  &__group {
    display: flex;
    align-items: start;
    gap: 50px;
  }
  &__menu {
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
    li a {
      font-size: 18px;
      line-height: 120%;
      &:hover {
        color: #F99D08;
      }
    }
    .nowrap {
      white-space: nowrap;
    }
  }
  &__phone {
    font-family: 'Montserrat';
    font-size: 20px;
    line-height: 120%;
    font-weight: 600;
    &:hover {
      color: #F99D08;
    }
  }
  &__info {
    font-size: 16px;
    line-height: 120%;
    color: #828282;
    margin-top: 8px;
  }
  &__mail {
    display: inline-block;
    font-family: 'Montserrat';
    font-size: 18px;
    line-height: 120%;
    text-decoration: underline;
    text-decoration-color: #FFD900;
    text-underline-offset: 6px;
    margin-top: 32px;
    &:hover {
      text-decoration: unset;
      color: #F99D08;
    }
  }
}

@media screen and (max-width: 1200px) {
  .footer {
    &__inner {
      gap: 30px;
    }
    &__right {
      flex-shrink: unset;
      gap: 30px;
    }
    &__group {
      gap: 30px;
    }
    &__phone {
      font-size: 18px;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    padding: 40px 0;
    &__inner {
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
    &__left {
      width: 100%;
      flex-direction: column;
      align-items: center;
    }
    &__right {
      width: 100%;
      max-width: 100%;
      flex-direction: column;
      align-items: center;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    &__group {
      flex-direction: column;
      align-items: center;
    }
  }
}