.header {
  padding: 10px 0 30px;
  font-family: 'Montserrat';
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    border-bottom: 1px solid #D8D8D8;
    padding-bottom: 12px;
  }
  &__logo {
    width: 193px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__search {
    position: relative;
    input {
      width: 410px;
      height: 40px;
      background: transparent;
      border: 1.5px solid transparent;
      border-bottom-color: #A2A2A2;
      padding: 0 25px 0 0;
      font-size: 16px;
      line-height: 100%;
      &::placeholder {
        color: #9F9F9F;
      }
    }
    &-btn {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 19px;
      img {
        width: 100%;
      }
    }
  }
  &__phone {
    font-size: 18px;
    line-height: 100%;
    font-weight: 600;
    &:hover {
      color: #F99D08;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-top: 10px;
  }
  &__social {
    width: 32px;
    height: 32px;
    background: #F9E508;
    border-radius: 50%;
    &:hover {
      opacity: 0.7;
    }
    img {
      max-width: 60%;
    }
  }
  &__bottom {
    padding-top: 12px;
  }
  &__menu {
    display: flex;
    gap: 70px;
    li a {
      font-size: 18px;
      line-height: 100%;
      &:hover {
        color: #F99D08;
      }
    }
  }
  &__item {
    position: relative;
    &--active {
      .header__item-btn {
        transform: rotate(-180deg);
      }
      .header__item-hidden {
        transform: translateY(0);
        opacity: 1;
        pointer-events: auto;
      }
    }
    &-show {
      display: flex;
      align-items: center;
      gap: 5px;
    }
    &-title {
      font-weight: 600;
    }
    &-btn {
      width: 12px;
      display: flex;
      align-items: center;
      transition: all 0.3s;
      svg {
        width: 100%;
        height: auto;
      }
    }
    &-hidden {
      position: absolute;
      z-index: 2;
      top: 100%;
      left: 0;
      transform: translateY(-5px);
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &-list {
      max-width: 300px;
      width: max-content;
      display: grid;
      grid-template-columns: 100%;
      gap: 12px;
      background: #fff;
      box-shadow: 4px 4px 23px -3px rgba(34, 60, 80, 0.2);
      padding: 20px;
      margin-top: 10px;
    }
  }
  &__mob {
    display: none;
  }
}
.burger {
  display: none;
}

@media screen and (max-width: 1300px) {
  .header {
    &__menu {
      justify-content: space-between;
      gap: 15px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    &__menu {
      li a {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &__top {
      .header__search {
        display: none;
      }
    }
    &__group {
      display: flex;
      align-items: center;
      gap: 30px;
    }
    &__bottom {
      display: none;
    }
  }
  .header__mob {
    position: fixed;
    z-index: 10;
    top: 0;
    right: -105%;
    width: 100%;
    max-width: 400px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 60px;
    background: rgba(0, 0, 0, 0.8);
    padding: 100px 15px 50px;
    color: #fff;
    transition: all 0.3s;
    overflow-y: auto;
    &--active {
      right: 0;
    }
    .header__menu {
      flex-direction: column;
      gap: 30px;
    }
    .header__search {
      input {
        width: 100%;
        color: #fff;
        opacity: 1;
        &::placeholder {
          color: #fff;
          opacity: 0.7;
        }
      }
    }
    .header__item {
      &--active {
        .header__item-hidden {
          display: block;
        }
      }
      &-btn {
        svg path {
          fill: #fff;
        }
      }
      &-hidden {
        position: static;
        transform: unset;
        display: none;
        opacity: 1;
      }
      &-list {
        gap: 15px;
        background: transparent;
        padding: 0 0 0 15px;
        margin-top: 15px;
      }
    }
  }
  .burger {
    position: relative;
    z-index: 12;
    display: block;
    width: 30px;
    height: 20px;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 2px;
      background: #1B1C1D;
      border-radius: 3px;
      transition: all 0.3s;
    }
    span:nth-child(1) {
      top: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
    &--active {
      span {
        background: #fff;
      }
      span:nth-child(1) {
        top: 9px;
        transform: rotate(-45deg);
      }
      span:nth-child(2) {
        display: none;
      }
      span:nth-child(3) {
        bottom: 9px;
        transform: rotate(45deg);
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__top {
      gap: 20px;
    }
    &__logo {
      width: 160px;
    }
    &__group {
      gap: 20px;
    }
    &__phone {
      font-size: 14px;
    }
    &__social {
      width: 28px;
      height: 28px;
    }
  }
  .header__mob {
    right: 0;
    max-width: 100%;
    opacity: 0;
    pointer-events: none;
    &--active {
      opacity: 1;
      pointer-events: auto;
    }
  }
}

@media screen and (max-width: 399px) {
  .header {
    &__top {
      .header__subgroup {
        display: none;
      }
    }
  }
}
